.resultData{
    display: flex;
    height: fit-content;
    padding: 8px;
    box-sizing: border-box;
    border-radius: 10px;
    gap: 8px;
    cursor: pointer;

    &:hover{
        background-color: #383838;
    }
    .frame{
        background-color: #2c2c2c;
        img{
            width: 50px;
            height: 50px;
            background-size: contain;
            background-position: 100%;
        }
    }
    &__title{
        display: -webkit-box;
        -webkit-line-clamp: 2;
        line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        word-wrap: break-word;
        overflow-wrap: anywhere;
        white-space: normal;
    }

    &__definition{
        font-size: 12px;
    }
}