.playlist{
    user-select: none;

    height: 64px;
    width: calc(100% + 8px);
    color: white;
    box-sizing: border-box;
    display: flex;
    gap: 10px;
    padding: 8px;
    position: relative;
    border-radius: 5px;

    &:hover{
        background-color: #1f1f1f;
        cursor: pointer;
    }

    &__state-button{
        position: absolute;
        width: 48px;
        height: 48px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 4px;
        opacity: 0.6;
    }
    &__cover{
        background-repeat: no-repeat;
        background-size: contain;
        background-position: 100%;

        border-radius: 5px;
        height: 48px;
        width: 48px;
        min-width: 48px;

        &:before{
            position: absolute;
            width: 48px;
            height: 48px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 4px;
            opacity: 0.6;
        }
    }
    &__cover-liked{
        background: url('/assets/photos/likedCollectionIcon.png') center/contain no-repeat;
        width: 100%;
        height: 100%;
        border-radius: 5px;
    }
    &__info{
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-overflow: hidden;
    }
    &__artist{
        display: -webkit-box;
        -webkit-line-clamp: 1;
        line-clamp: 1;
        -webkit-box-orient: vertical;
        word-wrap: break-word;
        overflow: hidden;
        overflow-wrap: anywhere;
        white-space: normal;
    }
    &__artist{
        display: flex;
        font-size: 13px;
        opacity: 0.7;
        p{
            margin: 0;
            padding: 0;
        }
    }
    &__audio-visualizer{
        position: absolute;
        background: url('../../../../public/staff/whenaudioplays.png') center/contain no-repeat;
        filter: brightness(0) saturate(100%) invert(84%) sepia(12%) saturate(7399%) hue-rotate(80deg) brightness(90%) contrast(85%);
        right: 16px;
        width: 18px;
        height: 18px;
    }
}